<template>
  <el-dialog
    @close="resetInfo"
    :title="goodsData.id>0?'':''"
    :visible.sync="info.isShow"
    fullscreen
  >
    <el-form ref="goodsForm" :model="goodsData" :rules="rules" label-width="100px">
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="first">
          <el-form-item label="" prop="first_cateid">
            <el-select v-model="goodsData.first_cateid" @change="firstCateChange">
              <el-option
                v-for="item in cateList"
                :key="item.id"
                :value="item.id"
                :label="item.catename"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="second_cateid">
            <el-select v-model="goodsData.second_cateid">
              <el-option
                v-for="item in secondCateList"
                :key="item.id"
                :value="item.id"
                :label="item.catename"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="goodsname">
            <el-input placeholder="" v-model="goodsData.goodsname"></el-input>
          </el-form-item>
          <el-form-item label="" prop="img">
            <el-upload
              action="#"
              list-type="picture-card"
              :auto-upload="false"
              :limit="1"
              :on-exceed="limitTips"
              :on-change="imgChange"
              :file-list="imgList"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="price">
            <el-input placeholder="" v-model="goodsData.price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="market_price">
            <el-input placeholder="" v-model="goodsData.market_price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="specsid">
            <el-select v-model="goodsData.specsid" @change="specsidChange">
              <el-option
                v-for="item in specsList"
                :key="item.id"
                :value="item.id"
                :label="item.specsname"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="specsattr">
            <!-- multiple:  -->
            <el-select v-model="goodsData.specsattr" multiple>
              <el-option
                v-for="(item,index) in specsAttrs"
                :key="index"
                :value="item"
                :label="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="isnew">
            <el-radio-group v-model="goodsData.isnew">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="ishot">
            <el-radio-group v-model="goodsData.ishot">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-radio-group v-model="goodsData.status">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <el-form-item prop="description" label-width="0">
            <VueWangeditor
              style="height:480px"
              ref="editor"
              id="editor"
              width="100%"
              v-model="goodsData.description"
            />
          </el-form-item>
        </el-tab-pane>
      </el-tabs>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="info.isShow = false"> </el-button>
      <el-button type="primary" @click="submitForm"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex";
import VueWangeditor from "vue-wangeditor";
export default {
  computed: {
    ...mapState({
      cateList: state => state.category.cateList,
      specsList: state => state.specs.specsList
    })
  },
  components: {
    VueWangeditor
  },
  // 
  props: ["info"],
  data() {
    // 
    const validDesc = (rule, value, callback) => {
      if (this.$refs.editor.getHtml() === "<p><br></p>") {
        return callback(new Error(""))
      }
      callback()
    }
    // 

    const validImg = (rule, value, callback) => {
      if (this.goodsData.img === "") {
        return callback(new Error(""))
      }
      callback()
    }
    return {
      // tab
      activeName: "first",
      // 
      secondCateList: [],
      // 
      specsAttrs: [],
      // 
      imgList: [],
      // 
      goodsData: {
        // id
        id: 0,
        // id
        first_cateid: "",
        // id
        second_cateid: "",
        // id
        specsid: "",
        // 
        specsattr: [],
        //
        goodsname: "",
        // 
        img: "",
        // 
        price: "",
        //  
        market_price: "",
        // 
        isnew: "",
        // 
        ishot: "",
        // 
        status: "",
        // 
        description: ""
      },
      // 
      rules: {
        goodsname: {
          required: true,
          message: "",
          trigger: "blur"
        },
        price: { required: true, message: "", trigger: "blur" },
        market_price: {
          required: true,
          message: "",
          trigger: "blur"
        },
        first_cateid: {
          required: true,
          message: "",
          trigger: "blur"
        },
        second_cateid: {
          required: true,
          message: "",
          trigger: "blur"
        },
        specsid: { required: true, message: "", trigger: "blur" },
        specsattr: {
          required: true,
          message: "",
          trigger: "blur"
        },
        isnew: { required: true, message: "", trigger: "blur" },
        ishot: { required: true, message: "", trigger: "blur" },
        status: { required: true, message: "", trigger: "blur" },
        description: { validator: validDesc, trigger: "blur" },
        img: { validator: validImg, trigger: "blur" }
      }
    }
  },
  created() {
    // 
    if (this.cateList.length === 0) {
      this.getCateListActoin()
    }
    // 
    if (this.specsList.length === 0) {
      this.getSpecsListAction({ page: 1, size: 100 })
    }
  },
  methods: {
    ...mapActions("category", ["getCateListActoin"]),
    ...mapActions("specs", ["getSpecsListAction"]),
    // 
    firstCateChange(id) {
      // 1-id
      const index = this.cateList.findIndex(item => item.id === id)
      this.secondCateList = this.cateList[index].children
    },
    // 
    // true: 
    // false: 
    specsidChange(id, flag = true) {
      // 1-id
      const index = this.specsList.findIndex(item => item.id === id)
      // 
      this.specsAttrs = this.specsList[index].attrs
      // , 
      if (flag) {
        // 
        this.goodsData.specsattr = []
      }
    },
    // 
    imgChange(file) {
      // 
      this.goodsData.img = file.raw
    },
    // 
    limitTips() {
      this.$error("")
    },
    // 
    submitForm() {
      this.$refs.goodsForm.validate(valid => {
        if (valid) {
          const fd = new FormData()
          //  ['a','b','c']=>a,b,c
          this.goodsData.specsattr = this.goodsData.specsattr.join(",")
          // 
          this.goodsData.description = this.$refs.editor.getHtml()
          for (let key in this.goodsData) {
            fd.append(key, this.goodsData[key])
          }
          const url = this.goodsData.id > 0 ? "/goodsedit" : "/goodsadd"
          this.$http.post(url, fd).then(res => {
            if (res.code === 200) {
              this.$success(res.msg)
              // 
              return this.resetInfo()
            }
            this.$error(res.msg)
          })
        }
      })
    },
    // 
    resetInfo() {
      //  1-
      this.info.isShow = false
      //  2-
      this.goodsData = {
        id: 0,
        goodsname: "",
        first_cateid: "",
        second_cateid: "",
        specsattr: [],
        specsid: "",
        img: "",
        status: "",
        isnew: "",
        ishot: "",
        price: "",
        market_price: "",
        description: ""
      }
      // 
      this.imgList = []
      // 
      this.$refs.editor.setHtml("")
      // 3-: 
      this.$emit("updateList")
    },
    // 
    setInfo(obj) {
      // 
      delete obj.firstcatename
      delete obj.secondcatename
      this.goodsData = { ...obj }
      // 
      this.goodsData.specsattr = this.goodsData.specsattr.split(",")
      // 
      this.firstCateChange(obj.first_cateid)
      // change
      this.specsidChange(obj.specsid, false)
      // 
      this.imgList = obj.img !== "" ? [{ url: this.$imgBaseUrl + obj.img }] : []
      // 
      setTimeout(() => {
          this.$refs.editor.setHtml(obj.description)
      }, 200);
    }
  }
}
</script>

<style>
</style>


